<template>
	<!-- 查询表单 -->
	<el-form ref="form" :model="form" label-width="80px" class="searchForm">
		<div>
			<img class="font" src="../../image/font.png" />
		</div>
		<!-- 姓名输入 -->
		<div class="inputText">
			<el-form-item label="姓名">
				<el-input v-model="form.name" prefix-icon="el-icon-user-solid"></el-input>
			</el-form-item>
		</div>
		<!-- 身份证号输入 -->
		<div class="inputText" style="margin-top: 40px;">
			<el-form-item label="身份证号">
				<el-input v-model="form.name" prefix-icon="el-icon-postcard"></el-input>
			</el-form-item>
		</div>
		<!-- 查询日期 -->
		<div class="inputText" style="margin-top: 40px;">
			<el-form-item label="查询日期">
				<el-col :span="11">
					<el-date-picker v-model="value2" type="datetimerange" :picker-options="pickerOptions"
						range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" align="right">
					</el-date-picker>
				</el-col>
			</el-form-item>
		</div>
		<!-- 按钮 -->
		<el-form-item>
			<el-button type="primary" class="button" @click="onSubmit" style="background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);">
				查 询</el-button>
			<el-button class="button" style="margin-left: 90px; background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);">
				取 消</el-button>
		</el-form-item>
	</el-form>
</template>

<script>
	export default {
		data() {
			return {
				pickerOptions: {
					shortcuts: [{
						text: '最近一周',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '最近一个月',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
							picker.$emit('pick', [start, end]);
						}
					}, {
						text: '最近三个月',
						onClick(picker) {
							const end = new Date();
							const start = new Date();
							start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
							picker.$emit('pick', [start, end]);
						}
					}]
				},
				value1: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
				value2: '',
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				},
			}
		},
		methods: {
			onSubmit() {
				console.log('submit!');
			}
		}
	}
</script>

<style>
	.searchForm .inputText {
		width: 480px;
		margin-left: 30%;
		margin-top: 50px;
	}
	
	.searchForm .button{
		margin-left: 34%;
		width: 110px;
		height: 50px;
		margin-top: 30px;
		font-size: 17px;
		border: none;
	}
	.searchForm .font{
		margin-left: 470px;
	}
	
</style>
