<template>
	<div class="background">
		<div class="login_box">
			<!-- 头像区域 -->
			<div class="avatar_box">
				<img src="../image/Login.png" alt="">
			</div>
			<!-- 登陆表单 -->
			<el-form ref="loginFormRef" class="login_form" :model="loginForm" :rules="loginFormRules">
				<!-- 用户名 -->
				<el-form-item prop="username">
					<div class="inputText">
						<el-input id="username" prefix-icon="el-icon-user" v-model="loginForm.username"
							placeholder="Username"></el-input>
					</div>
				</el-form-item>
				<!-- 密码 -->
				<el-form-item prop="password">
					<div class="inputText" style="margin-top: -40px;">
						<el-input type="password" v-model="loginForm.password" prefix-icon="el-icon-lock"
							placeholder="Password"></el-input>
					</div>
				</el-form-item>
				<el-button type="LOGIN" round class="inputButton" @click="login()">登 陆</el-button>
				<div id="signIn" @click="register()">
					<el-link type="primary">
						没有账号？点击注册
					</el-link>
				</div>
			</el-form>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'Login',
		data() {
			var name = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请输入用户名'));
				} else {
					if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleForm.validateField('checkPass');
					}
					callback();
				}
			};
			return {
				// 登录表单数据
				loginForm: {
					username: '',
					password: '',
				},
				// 表单验证规则
				loginFormRules: {
					// 验证用户名是否合法
					username: [{
							required: true,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请输入用户名',
							trigger: 'blur'
						},
						{
							min: 4,
							max: 10,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0长度在 4 到 10 个字符',
							trigger: 'blur'
						}
					],
					// 验证密码是否合法
					password: [{
							required: true,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0请输入密码',
							trigger: 'blur'
						},
						{
							min: 6,
							max: 15,
							message: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0长度在 6 到 15 个字符',
							trigger: 'blur'
						}
					]
				}
			}
		},
		methods: {
			login() {
				this.$refs.loginFormRef.validate(async valid => {
					if (!valid) {
						this.$refs.loginFormRef.resetFields()
						this.$message.error('登陆失败');
					}
					let formData = new FormData();
					for (let key in this.loginForm) {
						formData.append(key, this.loginForm[key]);
						console.log(formData.get(key));
					}

					this.$axios
						.post('https://120.25.76.71/api/login', {
							"username": this.loginForm.username,
							"password": this.loginForm.password
						})
						.then(response => {
							this.$message.success('登陆成功')
							this.$router.push('./home');
						}, error => {
							this.$message.error('登陆失败')
						})
				});
			},
			register() {
				this.$router.push('./register');
			},
			home() {
				this.$router.push('./home');
			}
		}
	};
</script>

<style Lang="less" scoped="scoped">
	.background {
		width: 100%;
		height: 100%;
		/**宽高100%是为了图片铺满屏幕 */
		z-index: 1;
		position: absolute;
		background-image: url(../image/background.jpg);
		background-repeat: no-repeat;
		margin: 0;
		padding: 0;
	}

	.login_box {
		margin: 0 auto;
		margin-top: 200px;
		padding: 20px 50px;
		background-color: #00000090;
		width: 600px;
		height: 400px;
		border-radius: 10px;
		text-align: center;
	}

	.login_box .avatar_box {
		height: 100px;
		width: 100px;
		border: 1px solid #eee;
		border-radius: 50%;
		box-shadow: 0 0 10px #AAAAAA;
		position: absolute;
		left: 50%;
		transform: translate(-50%);
	}

	.login_box .avatar_box img {
		height: 100%;
		width: 100%;
		border-radius: 50%;
	}

	.login_box .login_form {
		margin-top: 50px;
	}

	.login_box .login_form .inputText {
		width: 300px;
		margin-left: 150px;
		padding-top: 70px;
	}

	.login_box .login_form .inputText i {
		color: white;
	}

	.login_box .login_form .inputText input {
		border: 0;
		width: 40%;
		font-size: 15px;
		color: #FFFFFF;
		background: #FFFFFF90;
		border-bottom: 2px solid #FFFFFF;
		padding: 5px 10px;
		margin-top: 10px;
	}

	.login_box .inputButton {
		border: 0px;
		width: 100px;
		height: 35px;
		color: white;
		margin-top: 30px;
		border-radius: 20px;
		background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
	}

	.login_box #signIn {
		margin: 5% auto;
	}
</style>
