<template onload="initMap()">
	<!-- 定义地图显示容器 -->
	<div id="container"></div>
</template>

<script>
	export default {
		data() {
			name: 'Map'
			return {}
		},
		methods: {
			// 地图初始化函数，本例取名为init，开发者可根据实际情况定义
			initMap() {
				// 定义地图中心点坐标
				var center = new window.TMap.LatLng(39.984120, 116.307484)
				// 定义map变量，调用 TMap.Map() 构造函数创建地图
				var map = new window.TMap.Map(document.getElementById('container'), {
					center: center, // 设置地图中心点坐标
					zoom: 17.2, // 设置地图缩放级别
					pitch: 43.5, // 设置俯仰角
					rotation: 45 ,// 设置地图旋转角度
				})
				console.log(map)
				console.log(window)
			}
		},
		mounted() {
			this.initMap()
		}
	}
</script>

<style>
	#container {
		/*地图(容器)显示大小*/
		/*设置为全屏，位于最下层*/
		width: 89.5%;
		height: 93.5%;
		position: absolute;
		top: 61px;
		bottom: 0px;
		left: 200px;
		right: 0px;
		z-index: 1;
	}
</style>
